document.addEventListener('DOMContentLoaded', function () {
    document.querySelectorAll('.add-strategy').forEach(button => {
        button.addEventListener('click', function () {
            const category = this.dataset.category;
            const list = document.querySelector(`.strategies-list[data-category="${category}"]`);
            const newItem = document.createElement('li');
            newItem.innerHTML = '<input type="text" name="advice_lab_pricing_strategies[' + category + '][]" value=""><button type="button" class="remove-strategy button">Remove</button>';
            list.appendChild(newItem);
        });
    });

    document.querySelectorAll('.strategies-list').forEach(list => {
        list.addEventListener('click', function (e) {
            if (e.target.classList.contains('remove-strategy')) {
                e.target.closest('li').remove();
            }
            if(e.target.classList.contains('strategy-label')){
                e.target.closest('li')?.querySelector('input')?.setAttribute('type', 'text');
                e.target.closest('li')?.querySelector('span')?.remove();
            }
        });
    });
});
